.impression {
  display: flex;
  flex-direction: column;
  padding: 24px 0;
  margin: 0;
  width: 100%;
  .last-element-margin-bottom();

  &::after {
    content: "";
    position: absolute;
    height: 1px;
    left: 0;
    right: 0;
    top: 0;
    background: #e5e5e5;
  }

  @media (min-width: @tablet-width) {
    width: 648px;
    padding: 63px 0 48px 0;

    &::before {
      content: "";
      position: absolute;
      height: 1px;
      right: 0;
      width: 123px;
      top: 75px;
      background: #e5e5e5;
    }

    &::after {
      content: "";
      position: absolute;
      height: 1px;
      left: 0;
      width: 123px;
      top: 75px;
      background: #e5e5e5;
    }
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    padding: 71px 0 47px 0;

    &::before {
      width: 280px;
    }

    &::after {
      width: 280px;
    }
  }
}

.impression__wrapper {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.impression__label {
  .title();
  display: flex;
  margin-bottom: 16px;
  text-transform: none;

  @media (min-width: @tablet-width) {
    width: 280px;
    margin-bottom: 26px;
  }
}

.impression__title {
  .title();
  text-align: center;
  margin-bottom: 20px;
  margin-top: 0;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    margin-bottom: 28px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 45px;
  }
}
